.name{margin-top:0rem;margin-right:auto}}.profession{z-index:-1;width:100%;text-align:left;display:flex;align-items:center;cursor:default;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.profession h1{color:#c3cccf;font-size:clamp(40px, 8vw, 80px);font-family:"Poppins", sans-serif;font-weight:bold}.profession h1 span:last-child{color:#f4bc46}@media (max-width: 960px){.profession h1{font-size:clamp(40px, 6vw, 80px)}}.profession span{display:inline-block}.profession span:hover{color:#58e6d7}.profession .greeting-chars.animated{-webkit-animation:colorChange 1s;animation:colorChange 1s;color:#000}@media (max-width: 1200px){.profession{margin-top:0rem;margin-right:auto}}.about-me{z-index:-1;width:40%;align-self:flex-start;text-align:left;margin-top:1rem;cursor:default;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.about-me h1{color:#a1a9ac;font-size:clamp(20px, 1vw, 30px);font-family:'Poppins', sans-serif;font-weight:500;margin-left:0.3rem;letter-spacing:1px}@media (max-width: 960px){.about-me h1{font-size:clamp(20px, 2.5vw, 65px)}}@media (max-width: 480px){.about-me h1{font-size:clamp(12px, 3.8vw, 90px)}}@media (max-width: 1200px){.about-me{margin-top:0rem;margin-right:auto}}@media (max-width: 960px){.about-me{margin-top:0rem;width:80%;height:12rem}}@media (max-width: 480px){.about-me{width:90%;height:24rem;margin-top:1rem}.about-me h1{margin-left:0.1rem}}@-webkit-keyframes colorChange{0%{scale:1;transform:rotate(0deg);text-shadow:0 0 0px #d44729,0 0 0px #d44729,0 0 0px #d44729,0 0 0px #d44729}50%{scale:0.8;transform:rotate(-360deg);text-shadow:0 0 1px #d44729,0 0 7.5px #d44729,0 0 15px #d44729,0 0 50px #d44729}100%{scale:1;transform:rotate(0deg);text-shadow:0 0 0px #d44729,0 0 0px #d44729,0 0 0px #d44729,0 0 0px #d44729}}@keyframes colorChange{0%{scale:1;transform:rotate(0deg);text-shadow:0 0 0px #f4bc46,0 0 0px #f4bc46,0 0 0px #f4bc46,0 0 0px #f4bc46}50%{scale:0.8;transform:rotate(-3